.legend-countries {
  display: flex;
}

.legend-countries-colors {
  width: 40px;
  height: 500px;
  background: linear-gradient(#ffffd9 0%, #4cb7c1 50%, #091e5a 100%);
  border-radius: 3px;
}

.legend-countries-labels {
  flex-direction: column;
  justify-content: space-between;
  margin-left: 5px;
  display: flex;
}

@media screen and (max-width: 768px) {
  .legend-countries {
    flex-direction: column;
  }

  .legend-countries-colors {
    width: 100%;
    height: 30px;
    background: linear-gradient(90deg, #ffffd9 0%, #4cb7c1 50%, #091e5a 100%);
  }

  .legend-countries-labels {
    flex-direction: row;
  }
}

.map-container {
  border: 1px solid #09131b;
  position: relative;
  overflow: hidden;
}

.filters-container {
  margin-bottom: 30px;
}

#map-tooltip {
  font-size: 2.2rem;
  font-weight: 600;
  position: absolute;
  top: 10px;
  right: 15px;
}

#map-reset {
  height: 40px;
  color: #f8fcff;
  cursor: pointer;
  background-color: #35a7c2;
  border: none;
  border-radius: 3px;
  padding: 0 20px;
  font-size: 1.6rem;
  font-weight: 600;
  transition: background-color .25s, opacity .25s;
  position: absolute;
  top: 50px;
  right: 15px;
}

#map-reset.hidden {
  opacity: 0;
  pointer-events: none;
}

#map-reset:hover, #map-reset:focus {
  background-color: #207ab5;
}

@media screen and (max-width: 768px) {
  #map-reset {
    padding: 0 10px;
    top: 0;
    right: 0;
  }
}

.axis-x text {
  font-family: Open Sans, sans-serif;
  font-size: 1.6rem;
}

.selection {
  fill: #35a7c2;
  fill-opacity: .3;
  stroke: none;
}

.map-france-container {
  max-width: 800px;
  margin: 0 auto;
}

